
.Menu{
  position: absolute;
  text-align: center;
  width: 100%;
  height: 100%;


  background-image: url(/src/image/Textura.png);
  background-position: center;
  background-repeat: repeat-y;
  overflow:scroll;

}
.Header{
  background-color:   #121212;;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  z-index: 1;
  padding: 5px;
  width: 100%;
  overflow:scroll;


}

 
.Header::-webkit-scrollbar{
  display: none;
}

.Nav-links{
  margin-left: auto ;
  margin-right: auto;
  list-style: none;
}

.Header .Nav-links li{
  display: inline;
}

.Link{
  font-family: 'Sancreek', cursive;
  text-transform: uppercase;
  font-size: 90%;
  color:  rgb(244, 211, 17);
  text-decoration: none;
  padding: 0px 2vh;
}

.linkactive{
  color:  white ;
}


html{
  scroll-behavior: smooth;
}

section[id]{
  scroll-margin: 10vh;
}

.Logo-menu{
  position: relative;
  background-color: black;
  height: 20%;

  margin-top: 30px;
  margin-bottom: 30px;

}

.button-menu{
  border:  0;
  font-family: 'Sancreek', cursive;
  text-transform: uppercase;
  width: 100%;
  height: 100px;
  margin-top: 10px;
  background: rgba(0, 0, 0, 0.25);
  font-size: 200%;
  color:  rgb(244, 211, 17);
}

.button-menu-hover{
  border:  0;
  font-family: 'Sancreek', cursive;
  text-transform: uppercase;
  width: 100%;
  height: 100px;
  margin-top: 10px;
  background: rgba(0, 0, 0, 0.5);
  font-size: 200%;
  color:  rgb(244, 211, 17);
}



.category-tittle{
  position: relative;
  margin-top: 80px;
  margin-bottom: 25px;
  text-align: center;
  font-family: 'Sancreek', cursive;
  text-transform: uppercase;
  font-size: 225%;
  color:  rgb(244, 211, 17);

}

.item{
  position: relative;
  width: 85%;
  max-width: 500px;
  min-height: 140px;
  text-align: center;

  
  border-radius: 2vh;
  background: #121212;
  text-align: justify;
  margin-top: 2%;
  margin-left: auto;
  margin-right: auto;

}


.top-info{
   padding-bottom: 0;
   display: grid;
   grid-template-columns: 75% 25%;
}

.botton-info{
  position: absolute;
  top: 25%;
  width: 100%;
}

.tittle-item{
  text-align: left;
  padding-left: 5%;
  font-family: 'Sancreek', cursive;
  text-transform: uppercase;
  font-size: 120%;
  color:  rgb(244, 211, 17);
}

.price-item{
  padding-right: 15%;
  text-align: right;
  font-family: 'Sancreek', cursive;
  text-transform: uppercase;
  font-size: 110%;
  color:  rgb(255, 255, 255);
}




.price-item::first-letter{
  font-family: 'Sancreek', cursive;
  text-transform: uppercase;
  font-size: 100%;
  color:  rgb(244, 211, 17);
}



.description-item{
  position: absolute;
  margin-left: 4%;
  margin-right: 5%;  
  font-family: 'Barlow', sans-serif;
  text-transform: initial;
  font-size: 100%;
  color:  rgb(255, 255, 255);
}


.overlay{
  top: 50%;
  left: 50%;
  width: 100%;
  height: 100vh;
  transform: translate(-50%, -50%);
  position: fixed;
  z-index: 1;
  background: rgba(0, 0, 0, 0.8);
}

.Container-Detail{
  top: 50%;
  left: 50%;
  position: fixed;
  transform: translate(-50%, -50%);
  width: 80%;
  height: 90%;
  max-height: 450px;
  max-width: 300px;
  background: #121212;
  border-radius:5%;
}

.imagen-detail{
  width: 100%;
}

.top-info-detail{
  padding-bottom: 0;
  display: grid;
  grid-template-columns: 75% 25%;
}

.botton-info-detail{
  position: absolute;
  top: 76%;
  width: 100%;
  text-align: justify;
}